.bg-image-profile {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0);
}

.background-hero {
  position: relative;
  display: block;
  background-color: #4452a0;
  width: 100%;
  height: 160px;
  z-index: -99;
  margin-top: -60px;
}

.image-container {
  overflow: hidden;
}

.image-container img {
  height: auto;
}

.icon-container {
  margin-top: 12px;
  margin-left: 12px;
  color: white;
}

.disable-select {
  user-select: none;
  pointer-events: none;
}

.profile-container {
  max-width: 700px;
  min-width: 300px;
  width: 70%;
  height: 455px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.profile-container > * {
  margin-left: 5vw;
  margin-right: 5vw;
}

.edit-container {
  max-width: 700px;
  min-width: 300px;
  width: 70%;
  height: 500px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.edit-container > * {
  margin-left: 5vw;
  margin-right: 5vw;
}

.title-container {
  max-width: 700px;
  min-width: 300px;
  width: 70%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  margin-top: -120px;
  color: white;
  font-size: x-large;
}

.set-top {
  top: -450px;
  position: relative;
}
.set-top h2 {
  margin-top: 4px;
}

.set-maxheight {
  max-height: 500px;
}

.name-id-text {
  margin-top: 20px;
  margin-bottom: 20px;
}

.name-id-text h1,
.name-id-text h2 {
  margin-right: 10px;
  display: inline;
}

@media (max-width: 600px) {
  .name-id-text h1 {
    margin-right: 10px;
    display: block;
  }

  .name-id-text h2 {
    margin-right: 10px;
    margin-top: -25px;
    margin-bottom: -10px;
    display: block;
  }

  .name-id-text h1,
  .name-id-text h2 {
    text-align: center;
  }

  .profile-container {
    max-width: 700px;
    min-width: 300px;
    width: 70%;
    height: 400px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
}

.typo-container {
  margin-top: 50px;
}

.set-to-center {
  justify-content: center;
  align-items: center;
}

.set-to-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  margin-bottom: 20px;
}

.info-text {
  display: flex;
  align-items: center;
}

.info-text p {
  margin-top: 0;
  margin-bottom: 0;
}

.font-body {
  color: gray;
  font-size: medium;
  flex-grow: 1;
  margin-left: 5vw; /* 왼쪽에서 5vw 만큼 떨어지도록 추가 */
}

.font-title {
  font-weight: bold;
  font-size: large;
  width: 120px; /* 시작점을 일정하게 유지하기 위한 너비 설정 */
}
